  background-color: #f0f0f0;
}

/* Form Styles */
.register-form,
.login-form,
.forgot-password-form {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  text-align: center;
  transition: box-shadow 0.3s ease;
}

.register-form h2,
.login-form h2,
.forgot-password-form h2 {
  margin-bottom: 20px;
  font-family: Arial, sans-serif;
  color: #333;
}

/* Input Fields */
.register-form input,
.login-form input,
.forgot-password-form input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.register-form input:focus,
.login-form input:focus,
.forgot-password-form input:focus {
  border-color: #00bfa6;
}

/* Buttons */
.register-form button,
.login-form button,
.forgot-password-form button {
  width: 100%;
  padding: 10px;
  background-color: #00bfa6;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.register-form button:hover,
.login-form button:hover,
.forgot-password-form button:hover {
  background-color: #019f87;
}

/* Links */
.login-link,
.register-link,
.reset-link {
  margin-top: 20px;
}

.login-link a,
.register-link a {
  color: #007bff;
  text-decoration: none;
  text-decoration: underline;
}

.reset-link a {
  color: #ff0400;
  text-decoration: none;
  text-decoration: underline;
}

.login-link a:hover,
.register-link a:hover,
.forgot-password-link a:hover,
.reset-link a:hover {
  text-decoration: underline;
}

/* Error and Success Messages */
.error {
  color: #ff3333;
  font-size: 14px;
  margin: -10px 0 10px;
}

.success {
  color: #28a745;
  font-size: 14px;
  margin: -10px 0 10px;
}

/* Navbar Styles */
.custom-navbar {
  background-color: rgb(2, 115, 159);
  padding: 0 1.5rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-img {
  max-width: 18%;
  height: auto;
}

/* Update navbar for mobile view */
@media (max-width: 768px) {
  .custom-navbar .navbar-nav {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: rgb(2, 115, 159);
    width: 100%;
    display: none;
    flex-direction: column;
  }

  .custom-navbar.active .navbar-nav {
    display: flex;
  }

  .navbar-toggle {
    display: block;
    cursor: pointer;
  }
}

.custom-navbar .navbar-brand {
  font-size: 36px;
  color: white;
  font-weight: bold;
}

.custom-navbar .navbar-nav .nav-link {
  color: white;
  font-size: 24px;
  transition: color 0.3s ease;
}

.custom-navbar .navbar-nav .nav-link:hover,
.custom-navbar .navbar-nav .nav-link.active {
  color: #31c27a;
  font-weight: bold;
}

/* Footer Styles */
.custom-footer {
  background-color: rgb(2, 115, 159);
  color: white;
  text-align: center;
  padding: 1.5rem 0;
  font-size: 18px;
}

/* Account Settings Layout */
.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 20px;
}

.profile-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.profile-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.profile-img:hover {
  transform: scale(1.05);
}

/* Hide the default file input */
.custom-file-input {
  visibility: hidden;
  width: 0;
  height: 0;
  position: absolute;
}

/* Style the label to look like a button */
.custom-file-label {
  display: inline-block;
  padding: 8px 15px;
  background-color: #0f8d1e;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
  transition: background-color 0.3s ease;
}

.custom-file-label:hover {
  background-color: #0056b3;
}

/* Style the container where selected file name will appear */
.file-name {
  margin-left: 15px;
  font-size: 1rem;
  color: #333;
}

/* Button Outline Styles */
.btn-outline-success {
  color: #00bfa6;
  border-color: #00bfa6;
  margin-top: 10px;
}

.btn-outline-success:hover {
  background-color: #00bfa6;
  color: white;
}

/* Section Titles */
.section-title {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 20px;
  color: #333;
}

/* Label Styles */
label {
  font-weight: 600;
  font-size: 1rem;
  color: #555;
  margin-bottom: 8px;
  display: block;
}

/* Input and Select Styles */
input,
select {
  height: 45px;
  font-size: 1rem;
  border-radius: 0.375rem;
  border: 1px solid #ced4da;
  padding: 0 15px;
  transition: all 0.2s ease-in-out;
}

input:focus,
select:focus {
  border-color: #00bfa6;
  box-shadow: 0 0 0 2px rgba(0, 191, 166, 0.2);
}

/* Button General Styles */
button {
  font-size: 1.2rem;
  padding: 12px 25px;
  border-radius: 5px;
  background-color: #00bfa6;
  color: white;
  border: none;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

button:hover {
  background-color: #019f87;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.15);
}

/* Section Styles */
.section {
  padding: 30px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
}

hr {
  margin: 50px 0;
  border: 1px solid #ddd;
}

button:hover,
.profile-img:hover {
  animation: pulse 0.3s ease-in-out;
}

/* HomePage.css */

/* CSS Variables for Consistent Theming */
:root {
  --primary-color: #28a745; /* Bootstrap Success */
  --secondary-color: #17a2b8; /* Bootstrap Info */
  --accent-color: #ffc107; /* Bootstrap Warning */
  --light-bg: #f9f9f9;
  --dark-text: #343a40;
  --font-family: "Roboto", sans-serif;
}

/* Global Styles */
body {
  font-family: var(--font-family);
  color: var(--dark-text);
}

/* Hero Section */
.hero-section h1 {
  font-size: 3rem;
  margin-top: 20px;
  font-weight: 700;
}

.hero-section img {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.hero-section .btn {
  padding: 10px 20px;
  font-size: 18px;
}

/* Testimonials Section */
.testimonials-section {
  background-color: var(--light-bg);
  padding: 30px 0;
}

.testimonials-section h2 {
  text-align: center;
}

.testimonials-section .testimonial {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .navbar-brand {
    font-size: 28px;
  }

  .navbar-nav .nav-link {
    font-size: 20px;
  }

  .hero-section h1 {
    font-size: 2rem;
  }

  .register-form,
  .login-form,
  .forgot-password-form {
    max-width: 90%;
  }

  .testimonials-section .testimonial {
    margin: 5px 0;
  }

  .profile-img {
    width: 200px;
    height: 200px;
  }
}

/* Product Card Container */
.card {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

/* Card Image Hover Effect */
.card-img-top {
  display: block;
  padding-left: 30px;
  margin: 0 auto;
  width: 100%;
  height: 400px;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.card:hover .card-img-top {
  transform: scale(1.05);
}

/* Card Body */
.card-body {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: ; */
}

/* Card Title */
.card-title {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

/* Card Price */
.card-text {
  font-size: 1rem;
  color: #07260e;
  margin-bottom: 15px;
}

/* Add to Cart Button */
.card-body .btn {
  background-color: #0d4cdf;
  color: white;
  font-size: 1rem;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
  display: inline-block;
}

.card-body .btn:hover {
  background-color: #019f87;
  transform: translateY(-2px);
}

/* Mobile-first approach */
.checkout-container {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  margin: auto;
}

.checkout-form,
.order-summary {
  padding: 1.5rem;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.order-summary {
  margin-top: 2rem;
}

.form-section-title,
.order-summary-title {
  color: #007bff;
  font-size: 1.25rem;
  margin-bottom: 1rem;
}

.form-actions {
  display: flex;
  flex-direction: column;
}

.checkout-button,
.back-button {
  width: 100%;
  margin-top: 1rem;
}

/* Tablet and larger screens */
@media (min-width: 768px) {
  .checkout-container {
    flex-direction: row;
    gap: 2rem;
    max-width: 1800px;
  }

  .checkout-form {
    flex: 2;
    max-width: 1200px;
  }

  .order-summary {
    flex: 1;
    max-width: 600px;
    height: fit-content;
    margin-top: 0;
  }

  .form-actions {
    flex-direction: row;
    justify-content: space-between;
  }

  .checkout-button,
  .back-button {
    width: auto;
    margin-top: 0;
  }
}

/* General Styling */

.enhanced-container {
  margin-top: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.card-title {
  font-size: 2rem;
  font-weight: bold;
  color: #3a3b3c;
  margin-bottom: 1rem;
}
.card {
  display: flex;
  flex-direction: column;
}

.card-body {
  flex: 1 0 auto;
}

.card-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.card-footer .btn {
  width: 100%;
  max-width: 200px;
}
/* Increase the size of categories link */
.category-link {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 0.5rem 1rem;
  transition: color 0.3s ease, background-color 0.3s ease;
}

.category-link:hover,
.category-link.active {
  background-color: rgba(0, 0, 0, 0.1);
  color: var(--primary-color);
}
.order-info {
  font-size: 14px;
}

.item-thumbnail {
  position: relative;
  display: inline-block;
}

.item-thumbnail img {
  border-radius: 5px;
  border: 1px solid #ddd;
}

.item-quantity {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #007bff;
  color: white;
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 50%;
}
.order-summary {
  border: none;
  background-color: #f8f9fa;
  padding: 20px;
}
.card-body {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
}
.summary-title {
  font-size: 1.5rem;
  color: #343a40;
  border-bottom: 2px solid #dee2e6;
  padding-bottom: 10px;
  padding-left: 0;
  align-items: left;
}

.subtotal-price,
.savings-amount,
.subtotal-after,
.delivery-free,
.total-amount,
.tax {
  font-weight: 600;
  color: #007bff;
  padding-left: 80px;
}

.savings {
  color: #dc3545;
}

.tax-details {
  font-size: 0.9rem;
}

.estimated-total strong {
  font-size: 1.25rem;
}

.small-note {
  font-size: 0.85rem;
  margin-top: 15px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.summary {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
}
